#goTop{
    z-index: 3;
    position: fixed;
    right: 30px;
    bottom: -100px;
    width: 60px;
    height: 60px;
    transition: all 0.6s ease-in-out;
    &.on{
        bottom: 30px;      
    }
 

    a {
        width: 100%;
        display: block;
        img {width: 100%;}
    }
}
